<template>
    <el-container class="layout heightfull w24">
        <div>
            <el-menu
                class="menu heightfull"
                router
                :default-active="$route.path"
                :unique-opened="true"
                active-text-color="#fff"
                text-color="#fff"
                :collapse="menu"
            >
                <div class="logo">
                    <img src="~@/assets/img/logo.png" height="40px" v-show="!menu" />
                    <img src="~@/assets/img/logo2.png" height="40px" v-show="menu" />
                </div>
                <el-submenu
                    v-for="item in aside"
                    :key="item.index"
                    :index="item.index"
                    @click.native="add($event,item.index)"
                >
                    <template slot="title">
                        <i :class="item.icon"></i>
                        <span slot="title">{{item.name}}</span>
                    </template>
                    <el-menu-item
                        class="sub-menu-item"
                        v-for="(page_item,page_index) in item.page_list"
                        :key="page_index"
                        :index="page_item.link"
                    >{{page_item.name}}</el-menu-item>
                </el-submenu>
            </el-menu>
        </div>

        <el-container class="heightfull">
            <el-header height="55px" class="header df aic jcsb">
                <i
                    :class="[!menu ? 'el-icon-s-fold' : 'el-icon-s-unfold', 'f32']"
                    @click="menu =!menu"
                ></i>

                <div class="header-right df aic">
                    <div class="mr30">
                        <img src="~@/assets/img/管理员.png" alt width="20px" />
                        <span>管理员</span>
                    </div>

                    <div class="mr30">
                        <img src="~@/assets/img/退出.png" alt width="20px" />
                        <span>退出</span>
                    </div>
                    <!-- TODO: 头像地址 -->
                    <el-avatar :src="require('@/assets/img/avatar.png')"></el-avatar>
                </div>
            </el-header>

            <el-main class="main df ffcn">
                <div class="mtb15">
                    <better-scroll class="better-scroll" v-bind=" { data: links } ">
                        <div class="content">
                            <el-tag
                                class="mr10 mt5"
                                v-for="(item,index) in links"
                                :key="item.name"
                                closable
                                :type="$route.path==item.url?'danger':''"
                                @close="closeTag(item.url,index)"
                                @click="back(item.url)"
                            >{{item.name}}</el-tag>
                        </div>
                    </better-scroll>
                </div>

                <div class="fg1">
                    <transition name="el-zoom-in-center" mode="out-in">
                        <router-view />
                    </transition>
                </div>
            </el-main>
        </el-container>
    </el-container>
</template>

<script>
import BetterScroll from "@/components/BetterScroll";
export default {
    name: "page",
    components: {
        BetterScroll
    },
    data() {
        return {
            menu: false,
            userInfo: {},
            aside: [
                {
                    index: "1",
                    icon: "el-icon-user",
                    name: "用户",
                    page_list: [
                        {
                            link: "/page/user/userList",
                            name: "用户列表"
                        },
                        {
                            link: "/page/user/structure",
                            name: "组织架构"
                        },
                        {
                            link: "/page/user/queryLogs",
                            name: "日志查询"
                        }
                    ]
                },
                {
                    index: "2",
                    icon: "el-icon-document",
                    name: "订单",
                    page_list: [
                        {
                            link: "/page/order/queryorder",
                            name: "订单检索"
                        }
                    ]
                },
                {
                    index: "3",
                    icon: "iconfont icon-yongjinguanli",
                    name: "佣金",
                    page_list: [
                        {
                            link: "/page/brokerage/queryBrokerage",
                            name: "佣金明细"
                        }
                    ]
                },
                {
                    index: "4",
                    icon: "iconfont icon-dianpu",
                    name: "店铺装修",
                    page_list: [
                        {
                            link: "/page/mini_program",
                            name: "小程序装修"
                        }
                    ]
                },
                {
                    index: "5",
                    icon: "iconfont icon-wuliaoguanli",
                    name: "物料",
                    page_list: [
                        {
                            link: "/page/classification-management",
                            name: "物料分类管理"
                        },
                        {
                            link: "/page/material-management",
                            name: "物料组管理"
                        },
                        {
                            link: "/page/material-group",
                            name: "物料管理"
                        }
                    ]
                },
                {
                    index: "6",
                    icon: "el-icon-goods",
                    name: "店铺管理",
                    page_list: [
                        {
                            link: "/page/shop/shopList",
                            name: "店铺列表"
                        },
                        {
                            link: "/page/shop/newShop",
                            name: "新增店铺"
                        }
                    ]
                },
                {
                    index: "7",
                    icon: "el-icon-star-off",
                    name: "库存管理",
                    page_list: [
                        {
                            link: "/page/stock/stockRecord",
                            name: "入库记录"
                        },
                        {
                            link: "/page/stock/stockCheck",
                            name: "库存查看"
                        }
                    ]
                },
                {
                    index: "8",
                    icon: "el-icon-present",
                    name: "优惠中心",
                    page_list: [
                        {
                            link: "/page/discountsCenter/adjustPrice",
                            name: "商品调价单"
                        },
                        {
                            link: "/page/discountsCenter/discountActivity",
                            name: "优惠活动管理"
                        }
                    ]
                },
                {
                    index: "9",
                    icon: "el-icon-user",
                    name: "推销员管理",
                    page_list: [
                        {
                            link: "/page/salesmanList",
                            name: "推销员列表"
                        },
                        {
                            link: "/page/newsalesman",
                            name: "新增推销员"
                        }
                    ]
                }
            ],
            links: [] //路由集合
        };
    },
    created() {
        this.links.push({
            url: this.$route.path,
            name: this.$route.meta.join(">")
        });
        this.userInfo = JSON.parse(localStorage.getItem("userInfo"));
    },
    mounted() {},
    watch: {
        "$route.path"() {
            for (let i in this.links) {
                let link_splice = true;
                this.aside.forEach(res => {
                    res.page_list.forEach(item => {
                        if (item.link == this.links[i].url) {
                            link_splice = false;
                        }
                    });
                });
                if (link_splice) this.links.splice(i, 1);
            }

            for (let i of this.links) {
                if (i.url === this.$route.path) return;
            }
            this.links.push({
                url: this.$route.path,
                name: this.$route.meta.join(">")
            });
        }
    },
    methods: {
        //点击关闭tag关闭
        closeTag(url, index) {
            if (this.links.length == 1) return;
            if (this.$route.path == url)
                this.$router.push(this.links[this.links.length - 2].url);
            this.links.splice(index, 1);
        },
        //点击返回
        back(url) {
            if (this.$route.path === url) return;
            this.$router.replace(url);
        },
        //点击登出触发
        backLogin() {
            this.$confirm("确认退出登录?", "提示", {
                confirmButtonText: "确定",
                cancelButtonText: "取消",
                type: "warning"
            }).then(() => {
                localStorage.removeItem("userInfo");
                this.$router.replace("/");
            });
        },

        add(e, index) {
            e.target.classList;
            console.log(e.target, index, e.target.classList);
        }
    }
};
</script>

<style scoped >
/* TAG: Main 背景颜色/固定高度 */
.main {
    background-color: #f1f2f6;
    padding: 0 20px 15px;
    position: relative;
}

.header {
    box-shadow: 0 1px 4px rgba(0, 21, 41, 0.08);
    position: relative;
    z-index: 3;
}

.logo {
    height: 55px;
    line-height: 55px;
    text-align: center;
}

.logo img {
    vertical-align: middle;
}

.menu {
    background-image: linear-gradient(to bottom, #00a1ff, #00c2ff);
}

.menu:not(.el-menu--collapse) {
    box-shadow: 2px 0 6px rgba(0, 21, 41, 0.35);
    position: relative;
    z-index: 3;
    border-right: none;
    width: 220px;
}

.iconfont {
    margin-right: 5px;
    width: 24px;
    display: inline-block;
    text-align: center;
    font-size: 18px;
}

/* ELEMENT_UI STYLE */
.menu /deep/ .el-submenu__title i {
    color: #fff;
}
.menu /deep/ .el-menu-item:focus {
    background-color: transparent;
}

/* TAG: 一级菜单 hover 颜色 */
.menu /deep/ .el-submenu__title:hover {
    background-color: transparent;
}
/* TAG: 二级菜单背景颜色 */
.menu /deep/ .el-menu--inline {
    background-color: transparent;
}
/* TAG: 二级菜单 hover 颜色 */
.menu /deep/ .el-menu-item:hover {
    background-color: transparent;
}
.header /deep/ i[class^="el-icon"] {
    cursor: pointer;
}

/* better-scroll */
.better-scroll > .content {
    white-space: nowrap;
    display: inline-block;
    height: 100%;
}
</style>


